import React from 'react';
import './App.css';

import { useSelector, useDispatch } from 'react-redux';

import Disclaimer from './Disclaimer';
import Offer from './Offer';

function App() {
 
  const dispatch = useDispatch();
  
  const onAcceptance = () => {
    dispatch({ type: 'DISCLAIMER_ACCEPTED' })
  }

  const hasAcceptedDisclaimer = useSelector(x => x.disclaimerAcceptedAt)

  return (
    <div className="App container">
      <Disclaimer isOpen={!hasAcceptedDisclaimer} onAcceptance={onAcceptance} />
      <header >
        <div className="App-Header row">
          <div className="col-xs-12 col-lg-6 text-center">
            <img 
              src="/logo.png" 
              style={{ height: 60 }}  
              alt="Logo" 
            />
          </div>
          <div className="col-xs-12 col-lg-6 text-center">
            <span className="App-Title">Compensation Explorer</span>
          </div>
        </div>
      </header>
      <main>
        { hasAcceptedDisclaimer && <Offer /> }
      </main>
      <footer style={{marginTop: 30}} className="small text-center">&copy; {new Date().getFullYear} Captivation Software, LLC - All rights reserved.</footer>
    </div>
  );
}

export default App;
