import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux'

import help from './help';
import store from './store'
import App from './App';
import * as serviceWorker from './serviceWorker';

import 'animate.css';
import '@fortawesome/fontawesome-free/css/all.css';
import './index.css';

// debugging of redux state
window.store = store;

window.help = help;

window.offer = ({ settings, ...offer }) => {
  store.dispatch({ type: 'OFFER_CHANGED', offer })
}

window.settings = settings => {
  store.dispatch({ type: 'SETTINGS_CHANGED', settings })
}

store.subscribe(() => {
  const { offerCode } = store.getState();
  if (offerCode) {
    const path = `${window.location.origin}?offer=${offerCode}`;
    window.history.pushState({ path }, '', path);
  }
})

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
