import { createStore } from 'redux';
import qs from 'query-string';
import codec from 'object-encode';

// default offer
const defaultOffer = {
    rate: 0.0,
    signingBonus: 0.0,
    hasInsurance: true,
    title: 'Software Engineer'
}

// default settings
const defaultSettings = {
    hours: 1880,
    waivedInsuranceRateAdjustment: 3.0,
    ptoAccrualRate: 0.0333333333333333,
    retirementContributionRate: 0.2, 
    medicalContribution: 5586.72, 
    hsaContribution: 3000,
    dentalContribution: 394.92, 
    visionContribution: 98.16, 
    lifeAndDisabilityContribution: 424.3938461538462 
}

const { offer: encodedOffer } = qs.parse(window.location.search);

const initialState = {
    offer: encodedOffer ? codec.decode_object(encodedOffer, 'base64') : defaultOffer,
    disclaimerAcceptedAt: localStorage.getItem('disclaimerAcceptedAt'),
    settings: defaultSettings
}

export default createStore(
  (state, action) => {

    switch(action.type) {
 
      case 'DISCLAIMER_ACCEPTED': 
        const disclaimerAcceptedAt = Date.now();
        localStorage.setItem('disclaimerAcceptedAt', disclaimerAcceptedAt)
        return { ...state, disclaimerAcceptedAt }

      case 'SETTINGS_CHANGED': 
          return { ...state, settings: { ...state.settings, ...action.settings }}
 
      case 'OFFER_CHANGED':
        const offer = { ...state.offer, ...action.offer };
        return { 
            ...state, 
            offer,
            offerCode: codec.encode_object(offer, 'base64'),
        }

      default:
        return state

    }
  }, 
  initialState
)