import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import CountUp from 'react-countup';
import './Offer.css'
import Wow from 'wow.js';

function Breakdown(props) {
    // Create our number formatter.
    const formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
    });

    return <div className="wow bounceIn">
        <h4 style={{marginTop: 50, marginBottom: 30 }}>Here's how it all breaks down...</h4>
        <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>
            {
                [
                    { description: `Wages${ props.bonus ? ' + Signing Bonus' : ''}`, value: props.wages, icon: 'dollar-sign' },
                    { description: 'PTO', value: props.ptoContribution, icon: 'umbrella-beach' },
                    { description: '401k Contribution', value: props.retirementContribution, icon: 'piggy-bank'},
                    { description: 'HSA Contribution', value: props.hsaContribution, icon: 'credit-card' },
                    { description: 'Medical Insurance', value: props.medicalContribution, icon: 'hospital' },
                    { description: 'Dental Insurance', value: props.dentalContribution, icon: 'tooth' },
                    { description: 'Vision Insurance', value: props.visionContribution, icon: 'glasses' },
                    { description: 'Disability and Life Insurance', value: props.lifeAndDisabilityContribution, icon: 'crutch' },

                ].filter(x => x.value).map(({ description, value, icon }, i) => (
                    <div key={description} className="wow bounceIn" style={{ flexBasis: '21%', height: 100, margin: '20px 20px 30px', animationDelay: `${1 + i*0.2}s`}}>
                        <i style={{marginBottom: 15}} className={`fas fa-2x fa-${icon}`} />
                        <h6>{description}</h6>
                        <h5>{formatter.format(value)}</h5>
                    </div>
                ))
            }
        </div>
    </div>
}


export default function Offer() {

    useEffect(() => {
        new Wow().init();
    })

    const offerCode = useSelector(x => x.offerCode)
    const offer = useSelector(x => x.offer)
    const baseRate = offer.rate
    const signingBonus = offer.signingBonus
    const hasInsurance = offer.hasInsurance
    const title = offer.title

    const { 
        hours, 
        waivedInsuranceRateAdjustment,
        ptoAccrualRate,
        retirementContributionRate, 
        lifeAndDisabilityContribution, 
        ...optionalContributions
    } = useSelector(x => x.settings);
    
    // calculate income
    const rate = hasInsurance ? baseRate : baseRate + waivedInsuranceRateAdjustment;
    const wages = rate * hours;
    const bonus = signingBonus 
    const ptoHours = ptoAccrualRate * hours
    const pto = rate * ptoHours;
    const income = wages + bonus + pto;
    const retirement = income * retirementContributionRate
    
    const medicalContribution = hasInsurance ? optionalContributions.medicalContribution : 0;
    const hsaContribution = hasInsurance ? optionalContributions.hsaContribution : 0;
    const dentalContribution = hasInsurance ? optionalContributions.dentalContribution : 0;
    const visionContribution = hasInsurance ? optionalContributions.visionContribution : 0;
    const healthPremiums = medicalContribution + hsaContribution + dentalContribution + visionContribution;
    const insurancePremiums = healthPremiums + lifeAndDisabilityContribution;
    const totalCompensation = income + retirement + insurancePremiums 
    
    
    return (
        <div className="Offer text-center" key={offerCode}>
          <div className="Offer-Summary">
            <section className="wow fadeInDown">
              <h5 style={{ marginBottom: 10 }}>We are pleased to offer you a {title} position earning</h5>
              <h1>
                <CountUp duration={1} end={rate} prefix="$" decimals={2} />/hour
              </h1>
              { 
                signingBonus > 0 && [
                  <h5 key="signingBonusLead">with a</h5>, 
                  <h1 key="signingBonus" style={{ fontSize: '3em'}}>
                    <CountUp duration={1} end={signingBonus} separator="," prefix="$" decimals={2} /> Signing Bonus
                  </h1>
                ] 
              }
            </section>
            <section className="wow fadeInDown">
              <h5>This translates to total compensation of</h5>
              <h1>
                <CountUp delay={1} duration={1} end={totalCompensation} separator="," prefix="$" decimals={2} />
              </h1>
              <h5>(assuming you take { 52 - hours/40 } weeks off this year)</h5>
            </section>
            <section className="wow fadeInDown">
              <h5>...and we would have contributed</h5>
              <h1>
                <CountUp delay={2} duration={1} end={retirement} separator="," prefix="$" decimals={2} />
              </h1>
              <h5>towards your retirement this year alone!</h5> 
            </section>

          </div>
          <section>
            <Breakdown 
                wages={wages}
                bonus={bonus} 
                ptoContribution={pto}
                medicalContribution={medicalContribution}
                retirementContribution={retirement} 
                hsaContribution={hsaContribution}
                dentalContribution={dentalContribution} 
                visionContribution={visionContribution}
                lifeAndDisabilityContribution={lifeAndDisabilityContribution}
            />
          </section>
        </div>
    )
}